    /* box-sizing: border-box; */
    margin:0%;
    padding: 0px;
    
}

body {
   
    line-height: 1.6;
    color: var(--primary-color);
    font-size: 1.1rem;
  }
  
  h1,
  h2,
  h3,
  h4 {
    line-height: 1.3;
  }
  
 
  .text{
    margin-left: 20px; 
  }
  
 
  
  .cullen {
      float:right;
    border-radius: 10px;
    max-width:400px;
    max-height: 600px;
  }
  
  
  
  
  /* showcase section */
  
  .section-showcase {
    margin: 2rem 0;
  }
  
  .section-showcase .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
    align-items: center;
    justify-content: center;
  }
  
  .section-showcase h1 {
    font-size: 4rem;
    color: var(--primary-color);
  }
  
  .section-showcase p {
    margin: 1rem 0;
  }





















